import React, { useState, useEffect } from 'react';
import { LabelList, ResponsiveContainer, Scatter, ScatterChart, Tooltip, XAxis, YAxis } from 'recharts';
import { get_dividends_rate } from './utils';
import { useParams } from 'react-router-dom';
import Title from './template/Title';
import { DividendsRatio } from './models';
import CustomGrid from './template/CustomGrid';

export interface DR {
  name: string,
  dividendsRatio: number
  index: number
}

const THRESHOLD = 10

export default function MarketDividends() {
  const [dividendRate, setDividendRate] = useState<DividendsRatio[]>([]);
  const params: { market: string } = useParams()

  useEffect(() => {
    get_dividends_rate(params.market).then(response => {
      let info = response as DR[]
      // use inverted number to sort ASC
      for (let index = 0; index < info.length; index++) {
        info[index].index = index
      }
      setDividendRate(info)
    })
  }, [params.market])

  return (
    <CustomGrid>
      <Title>{params.market} biggest dividends</Title>
      <ResponsiveContainer width="100%" height="100%">
        <ScatterChart
          width={400}
          height={400}
          margin={{
            top: 20,
            right: 20,
            bottom: 20,
            left: 20,
          }}
        >
          <XAxis type="category" dataKey="index" />
          <YAxis type="number" dataKey="dividendsRatio" name="ratio" unit="%" domain={[THRESHOLD, 'dataMax']} />
          <Tooltip cursor={{ strokeDasharray: '3 3' }} />
          <Scatter name="A school" data={dividendRate} fill="#8884d8">
            <LabelList dataKey="name" position="top"/>
          </Scatter>
        </ScatterChart>
      </ResponsiveContainer>
    </CustomGrid>
  )
}
