import React, { useState, useEffect } from 'react';
import { ResponsiveContainer, Scatter, ScatterChart, Tooltip, XAxis, YAxis } from 'recharts';
import { get_pe_rate } from './utils';
import { useParams } from 'react-router-dom';
import Title from './template/Title';
import CustomGrid from './template/CustomGrid';


export interface PER {
  name: string,
  priceEarningsRatio: number
  index: number
}

export default function MarketAnalysis() {
  const [peRatio, setPeRate] = useState<PER[]>([]);
  const params: { market: string } = useParams()

  useEffect(() => {
    get_pe_rate(params.market).then(response => {
      let info = response as PER[]
      var newInfo =  [] as PER[]
      // TODO: separate by industry
      // Remove ratios above 20
      for (let index = 0; index < info.length; index++) {
        if (info[index].priceEarningsRatio < 20) {
          info[index].index = index
          newInfo.push(info[index])
        }       
      }
      setPeRate(newInfo)
    })
  }, [params.market])

  return (
    <CustomGrid>
      <Title>{params.market} smallest P/E</Title>
      <ResponsiveContainer width="100%" height="100%">
        <ScatterChart
          width={400}
          height={400}
          margin={{
            top: 20,
            right: 20,
            bottom: 20,
            left: 20,
          }}
        >
          <XAxis type="number" dataKey="index" />
          <YAxis type="number" dataKey="priceEarningsRatio" name="ratio" unit="%" />
          <Tooltip content={<CustomTooltip />} />
          <Scatter name="A school" data={peRatio} fill="#8884d8"/>
        </ScatterChart>
      </ResponsiveContainer>
    </CustomGrid>
  )
}

function CustomTooltip ( props: any , aaa: {})  {
  let a = props as {payload: {payload: {name: string, priceEarningsRatio: number}}[]}

    if ((a == null) || ( a.payload == null) || ( a.payload[0] == null)) {
      return null
    } 
    return (
      <div className="custom-tooltip">
        <p className="label">Name: {`${a.payload[0].payload.name}`}</p>
        <p className="intro">P/E Ratio: {`${a.payload[0].payload.priceEarningsRatio}`}</p>
      </div>
    );
};